function getTooltip({
  object
}) {
  return (
    object && {
      html: `\
  <div><b>Edinburgh Bus Map</b></div>
  <div>${object.properties.decile} / SIMD decile</div>
  <div><b>Minutes difference between Dec 19 and Mar 20 (actual)</b></div>
  <div>${(Math.floor(object.properties.difference)/60).toFixed(2)} minutes</div>
  <div><b>Minutes difference between Dec 19 and Mar 20 (normalised)</b></div>
  <div>${(Math.floor(object.properties.difference_norm)/60).toFixed(2)} minutes</div>

  `
    }
  );
}

